import React from 'react'
import { Formik, Field } from 'formik'
import { navigate } from 'gatsby-link'
import validationSchema from './validationSchema'

const encode = (data) => {
  return Object.keys(data)
    .map(key => encodeURIComponent(key) + '=' + encodeURIComponent(data[key]))
    .join('&')
}

const ContactForm = () => {
  return (
    <Formik
      initialValues={{ name: '', email: '', message: '' }}
      validationSchema={validationSchema}
      onSubmit={(values, { setSubmitting }) => {
        fetch("/?no-cache=1", {                                 //eslint-disable-line
          method: 'POST',
          headers: { 'Content-Type': 'application/x-www-form-urlencoded' },
          body: encode({
            'form-name': 'contact',
            ...values,
          }),
        })
          .then(() => {
            navigate('/contact/success')
            setSubmitting(false)
          })
          .catch(error => {
            console.log(error)
            alert("Error: Please Try Again!");                            //eslint-disable-line
            setSubmitting(false)
          })
      }}
      render={({
        errors,
        touched,
        isSubmitting,
        handleSubmit,
        handleReset,
      }) => (


        <form
          name='contact'
          onSubmit={handleSubmit}
          onReset={handleReset}
          data-netlify='true'
          data-netlify-honeypot='bot-field'
          className="w-full max-w-sm"
          >
          <div className='md:flex md:items-center mb-6'>
            <label className='md:w-1/3 block text-gray-500 font-bold md:text-right mb-1 md:mb-0 pr-4'>Name</label>
            <div className='md:w-2/3'>
              <Field className='bg-gray-200 appearance-none border-2 border-gray-200 rounded w-full py-2 px-4 text-gray-700 leading-tight focus:outline-none focus:bg-white focus:border-purple-500' type='text' placeholder='Full Name' name='name' id='name' />
            </div>
          </div>
          {touched.name && errors.name && <small className='w-full text-red-600'>{errors.name}</small>}

          <div className='md:flex md:items-center mb-6'>
            <label className='md:w-1/3 block text-gray-500 font-bold md:text-right mb-1 md:mb-0 pr-4'>Email</label>
            <div className='md:w-2/3'>
              <Field className='bg-gray-200 appearance-none border-2 border-gray-200 rounded w-full py-2 px-4 text-gray-700 leading-tight focus:outline-none focus:bg-white focus:border-purple-500' type='email' placeholder='Email' name='email' id='email' />
            </div>
            {touched.email && errors.email && <small className='has-text-danger'>{errors.email}</small>}
          </div>

          <div className='md:flex md:items-center mb-6'>
            <label className='md:w-1/3 block text-gray-500 font-bold md:text-right mb-1 md:mb-0 pr-4'>Message</label>
            <div className='md:w-2/3'>
              <Field className='bg-gray-200 appearance-none border-2 border-gray-200 rounded w-full py-2 px-4 text-gray-700 leading-tight focus:outline-none focus:bg-white focus:border-purple-500' component='textarea' placeholder='Message' name='message' id='message' rows='6' />
            </div>
            {touched.message && errors.message && <small className='has-text-danger'>{errors.message}</small>}
          </div>

          <div className='field is-grouped is-pulled-right'>
            <div className='control'>
              <button className='button' type='reset'>Clear</button>
            </div>
            <div className='control'>
              <button className='button is-primary' type='submit' disabled={isSubmitting}>Submit</button>
            </div>
          </div>

          <div className="md:flex md:items-center mb-6">
            <div className="md:w-1/3"></div>
            <label className="md:w-2/3 block text-gray-500 font-bold">
              <input className="mr-2 leading-tight" type="checkbox" />
              <span className="text-sm">
                Send me your newsletter!
              </span>
            </label>
          </div>

          <div className="md:flex md:items-center">
            <div className="md:w-1/3"></div>
            <div className="md:w-2/3">
              <button className="shadow bg-purple-500 hover:bg-purple-400 focus:shadow-outline focus:outline-none text-white font-bold py-2 px-4 rounded" type="button">
                Sign Up
              </button>
            </div>
          </div>
        </form>
      )}
    />
  )
}

export { ContactForm }
